body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.sk-panel__content {
  max-height: 300px;
  overflow-y: auto;
  padding-right: 10px;
}

table a {
  text-decoration: none;
}

table em {
  background-color: yellow;
}

.sk-table td, .sk-table th {
  padding: .5em .5em;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



